

// Components
//
// Define common padding and border radius sizes and more.

$border-dark-theme-color: $gray-dark-theme-900 !default;

// stylelint-disable-next-line scss/dollar-variable-default
$border-theme-map: map-merge(
  $border-theme-map,
  (
    dark: (
      "border-color":  $border-dark-theme-color
    )
  ),
);

$component-dark-theme-color:  $body-dark-theme-color !default;
$component-dark-theme-bg:     $gray-dark-theme-800 !default;

$component-dark-theme-active-color:  $black !default;
$component-dark-theme-active-bg:     $primary-50 !default;
